@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,600);
body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', arial, sans-serif; }

.flex-spacer {
  flex: 1 1; }

.page-view {
  flex: 1 1;
  background-color: #60808B;
  width: 100vw; }

.Header {
  width: 100vw;
  height: 60px;
  background-color: #00202B;
  color: #FFFFFF;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 20px;
  box-sizing: border-box;
  font-size: 50px;
  font-weight: bolder;
  line-height: 50px; }
  .Header .tabs {
    font-size: 13px;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-start; }
    .Header .tabs a {
      text-decoration: none;
      color: #FFF;
      height: 100%;
      padding: 20px;
      box-sizing: border-box;
      text-transform: uppercase;
      display: flex;
      align-items: center;
      transition: text-shadow 0.3s ease-in-out; }
      .Header .tabs a:first-child {
        background-color: #CFCC00; }
      .Header .tabs a:hover {
        background-color: #F3F6F8;
        color: #333333;
        text-shadow: 0 1px 3px rgba(0, 0, 0, 0.3); }

.card {
  min-width: 100px;
  min-height: 100px;
  background-color: #FFF;
  border-radius: 3px;
  display: inline-block;
  padding: 10px;
  box-shadow: 0 15px 20px -8px rgba(0, 0, 0, 0.3); }
  .card input[type=text] {
    width: 500px;
    padding: 10px;
    font-size: 15px;
    outline: none;
    border-radius: 3px;
    border: 1px solid #CCC; }

.Home {
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 100%;
  flex-direction: column;
  flex-wrap: wrap; }
  .Home .button-box {
    display: flex;
    align-items: center;
    justify-content: space-between; }
    .Home .button-box a {
      display: inline-block;
      text-decoration: none;
      padding: 10px 20px;
      background-color: #00202B;
      margin: 5px;
      border-radius: 3px;
      transition: all 0.15s ease-in-out;
      color: #FFF;
      font-weight: bolder; }
      .Home .button-box a:hover {
        margin-bottom: 15px;
        box-shadow: 0 3px 10px rgba(0, 0, 0, 0.2); }

.display-card {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 5px;
  overflow: hidden;
  display: inline-block;
  position: fixed;
  transition: all 0.3s ease-in-out; }
  .display-card:hover {
    z-index: 1; }

.toolbox {
  background-image: url(/static/media/Toolbox.a63062db.png);
  top: 10vh;
  left: 10vw;
  width: 785px;
  height: 480px;
  box-shadow: 0 20px 15px -8px rgba(0, 0, 0, 0.3); }

.lessonbox {
  background-image: url(/static/media/LessonBox.972d7a5b.png);
  top: 60vh;
  left: 50vw;
  width: 500px;
  height: 300px;
  box-shadow: 0 15px 25px -3px rgba(0, 0, 0, 0.3); }

.devfest {
  background-image: url(/static/media/Devfest.5c5c4fc4.png);
  top: 55vh;
  left: 12vh;
  width: 400px;
  height: 260px;
  box-shadow: 10px 20px 35px -3px rgba(0, 0, 0, 0.3); }

.game-board {
  width: 800px;
  height: 500px;
  background-color: #FFF;
  position: relative;
  border-radius: 3px;
  box-shadow: 0 10px 50px rgba(0, 0, 0, 0.3);
  overflow: hidden; }
  .game-board .full-screen-button {
    width: 50px;
    height: 50px;
    border: none;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
    border-radius: 3px;
    position: absolute;
    top: 10px;
    right: 10px;
    outline: none;
    z-index: 1; }
    .game-board .full-screen-button svg {
      width: 100%;
      height: 100%; }
    .game-board .full-screen-button:hover {
      background-color: #CCC; }
  .game-board .Game {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden; }
    .game-board .Game .board-container {
      background-color: #FFF;
      position: absolute; }
      .game-board .Game .board-container .board {
        background-color: #CCEEDD; }
        .game-board .Game .board-container .board .water {
          border-radius: 100%;
          background-color: #AACCFF;
          position: absolute; }
        .game-board .Game .board-container .board .particle {
          width: 5px;
          height: 5px;
          border-radius: 100%;
          background-color: black;
          position: absolute; }
          .game-board .Game .board-container .board .particle.herbivore {
            background-color: #338833; }
    .game-board .Game .full-size {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%; }
    .game-board .Game .Controls {
      position: absolute;
      bottom: 0;
      left: 0;
      z-index: 2;
      display: flex; }
      .game-board .Game .Controls .menu-button {
        position: relative;
        display: inline-block; }
        .game-board .Game .Controls .menu-button > ul {
          display: none;
          position: absolute;
          bottom: 42px;
          left: 3px;
          width: 150px;
          border-radius: 3px;
          background-color: #FFF;
          font-size: 13px;
          margin: 0;
          padding: 5px;
          box-sizing: border-box; }
          .game-board .Game .Controls .menu-button > ul li {
            list-style-type: none;
            padding: 5px;
            transition: all 0.3s ease-in-out; }
            .game-board .Game .Controls .menu-button > ul li:hover {
              background-color: #CCC; }
        .game-board .Game .Controls .menu-button:hover > ul {
          display: block; }
        .game-board .Game .Controls .menu-button:hover .menu-icon {
          margin-top: 0; }
      .game-board .Game .Controls button, .game-board .Game .Controls .menu-icon {
        border: none;
        background-color: #FFF;
        cursor: pointer;
        transition: all 0.3s ease-in-out;
        border-radius: 3px;
        outline: none;
        padding: 10px 5px;
        font-size: 13px;
        margin: 3px; }
        .game-board .Game .Controls button:hover, .game-board .Game .Controls .menu-icon:hover {
          background-color: #CCC; }

.Content {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-items: center;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0; }

