.card {
	min-width: 100px;
	min-height: 100px;
	background-color: #FFF;
	border-radius: 3px;
	display: inline-block;
	padding: 10px;
	box-shadow: 0 15px 20px -8px rgba(0,0,0,0.3);

	input[type=text] {
		width: 500px;
		padding: 10px;
		font-size: 15px;
		outline: none;
		border-radius: 3px;
		border: 1px solid #CCC;
	}
}

.Home {
	display: flex;
	align-items: center;
	justify-content: space-around;
	height: 100%;
	flex-direction: column;
	flex-wrap: wrap;

	.button-box {
		display: flex;
		align-items: center;
		justify-content: space-between;

		a {
			display: inline-block;
			text-decoration: none;
			padding: 10px 20px;
			background-color: #00202B;
			margin: 5px;
			border-radius: 3px;
			transition: all 0.15s ease-in-out;
			color: #FFF;
			font-weight: bolder;

			&:hover {
				margin-bottom: 15px;
				box-shadow: 0 3px 10px rgba(0,0,0,0.2);
			}
		}
	}
}

.display-card {
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
	border-radius: 5px;
	overflow: hidden;
	display: inline-block;
	position: fixed;
	transition: all 0.3s ease-in-out;

	&:hover {
		z-index: 1;
	}
}

